html {
  font-size: 100%;
  scroll-behavior: smooth;
}

*,
::after,
::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul,
ol li {
  list-style-type: none;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  text-transform: capitalize;
}

input:focus-visible {
  outline: none;
}

input:focus,
input:active {
  border: 1px solid white;
}

a:focus {
  outline: none;
}

img {
  width: 100%;
  display: block;
  object-fit: cover;
}

h1,
h2,
h3 {
  margin-top: 0;
}

body {
  width: 100%;
  font-family: "Poppins", sans-serif;
  color: white;
}

h1 {
  position: absolute;
  left: -10000px;
}

.hero {
  min-height: 100vh;
  background: url("/images/bg-intro-mobile.png");
  background-color: #ff7a7a;
  background-position-x: center;
  background-position-y: 0;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5.5rem 1.5rem 4.25rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .hero {
    background: url("/images/bg-intro-desktop.png");
    background-color: #ff7a7a;
    background-position-x: center;
    background-position-y: 0;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media screen and (min-width: 1320px) {
  .hero {
    flex-direction: row;
    justify-content: space-between;
    gap: 2.8125rem;
    padding: 7.5625rem 10.3125rem;
  }
}

.hero-content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media screen and (min-width: 375px) {
  .hero-content {
    width: 20.4375rem;
  }
}
@media screen and (min-width: 768px) {
  .hero-content {
    width: 32.8125rem;
    gap: 1.5rem;
  }
}
@media screen and (min-width: 1320px) {
  .hero-content {
    justify-content: flex-start;
    width: 32.8125rem;
    margin-left: auto;
    margin-right: auto;
  }
}
.hero-content__header {
  text-align: center;
  font-size: 1.75rem;
  font-weight: 700;
  line-height: 2.25rem;
  letter-spacing: -0.018125rem;
}
@media screen and (min-width: 768px) {
  .hero-content__header {
    font-size: 3.125rem;
    line-height: 3.4375rem;
    letter-spacing: -0.0325rem;
  }
}
@media screen and (min-width: 1320px) {
  .hero-content__header {
    text-align: left;
  }
}
.hero-content__para {
  text-align: center;
  font-size: 1rem;
  line-height: 1.625rem;
  font-weight: 500;
}
@media screen and (min-width: 1320px) {
  .hero-content__para {
    text-align: left;
  }
}

@media screen and (min-width: 375px) {
  .hero-cta {
    width: 20.4375rem;
  }
}
@media screen and (min-width: 768px) {
  .hero-cta {
    width: 33.75rem;
  }
}
.hero-cta__card {
  width: inherit;
  margin-top: 4rem;
  background-color: #6055a5;
  border-radius: 0.625rem;
  padding: 1.125rem 4.125rem;
  text-align: center;
  line-height: 1.625rem;
  margin-bottom: 1.5rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  transition: 0.2s all ease-in;
}
.hero-cta__card:hover {
  background-color: #7168b1;
}
@media screen and (min-width: 1320px) {
  .hero-cta__card {
    margin-top: 0;
    padding: 1rem 7rem;
  }
}
.hero-cta__card-header {
  font-size: 0.9375rem;
  font-weight: 600;
}
.hero-cta__card-text {
  font-weight: 400;
}
.hero-cta__form {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  background-color: white;
  border-radius: 0.625rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  justify-content: left;
}
@media screen and (min-width: 840px) {
  .hero-cta__form {
    padding: 2.5rem;
    gap: 1.25rem;
  }
}
.hero-cta__form-terms {
  color: #b9b6d3;
  text-align: center;
  font-size: 0.6875rem;
  line-height: 1.625rem;
  font-weight: 500;
}
.hero-cta__form-terms-heading {
  font-weight: 700;
  color: #ff7a7a;
}
@media screen and (min-width: 375px) {
  .hero-cta__form-terms {
    width: 15.5625rem;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 768px) {
  .hero-cta__form-terms {
    width: 23.0625rem;
  }
}

.input-element {
  position: relative;
  transition: 0.2s all ease-in;
}
@media screen and (min-width: 840px) {
  .input-element {
    width: 100%;
  }
}
.input-element-text {
  width: 100%;
  padding: 0.9375rem 1.213125rem;
  border-radius: 0.3125rem;
  border: 1px solid #dedede;
  font-size: 0.875rem;
  line-height: 1.625rem;
  letter-spacing: 0.015625rem;
  font-weight: 600;
  color: #3e3c49;
  caret-color: #6055a5;
}
.input-element-text:focus, .input-element-text:active {
  border-color: #6055a5;
}
.input-element-text:focus ~ label {
  padding-right: 0.25rem;
  top: -0.8125rem;
  background-color: white;
  font-size: 0.625rem;
}
.input-element-text:not(:placeholder-shown) ~ label {
  padding-right: 0.25rem;
  top: -0.8125rem;
  background-color: white;
  font-size: 0.625rem;
}
.input-element-text-text:valid ~ .validation-icon {
  opacity: 0;
}
.input-element-text-text:valid ~ .validation-text {
  opacity: 0;
}
.input-element-label {
  padding-left: 0.375rem;
  transition: 0.2s all ease-in;
  position: absolute;
  top: 0.9375rem;
  left: 1.213125rem;
  color: #3e3c48;
  font-size: 0.875rem;
  line-height: 1.625rem;
  letter-spacing: 0.015625rem;
  font-weight: 600;
  cursor: text;
}
.input-element-label:focus {
  padding-right: 0.375rem;
  top: -0.8125rem;
  background-color: white;
  font-size: 0.625rem;
}
.input-element .validation-icon {
  position: absolute;
  content: url("../../../images/icon-error.svg");
  top: 0.9375rem;
  right: 1.213125rem;
  opacity: 0;
  transition: 0.2s all ease-in;
}
.input-element .validation-icon .show {
  opacity: 1;
}
.input-element .validation-text {
  transition: 0.2s all ease-in;
  font-size: 0.6875rem;
  font-weight: 500;
  line-height: 1.03125rem;
  color: #ff7a7a;
  text-align: right;
  margin-top: 0.375rem;
  opacity: 0;
}
.input-element .validation-text .show {
  opacity: 1;
}
.input-element .show {
  opacity: 1;
}

.btn {
  width: 100%;
  height: 3.5rem;
  border-radius: 0.3125rem;
  border: none;
  color: white;
  background-color: #38cc8c;
  transition: 0.2s all ease-in;
  cursor: pointer;
  font-size: 0.9375rem;
  font-weight: 600;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.0908818);
}
.btn:hover, .btn:focus {
  background-color: #78e2b4;
}
@media screen and (min-width: 840px) {
  .btn {
    letter-spacing: 0.0625rem;
  }
}